.About {
  float: right;
  margin-left: 10px !important;
}

.ImageUploader {
  padding-top: 10px;
}

.App {
  display: grid;
  justify-items: center;
}

.ImageCard {
  padding-top: 10px;
  padding-bottom: 30px;
}

.CaptionArea {
  background-color: white;
  height: 50px;
  box-shadow: 2px 2px 2px #e5d3d3;
}

#CardImage {
  width: 614px;
  box-shadow: 2px 2px 2px #e5d3d3;
}

ul {
  list-style-type: none;
  font-size: 1.3rem;
}

p {
  padding: 15.5px 15.5px 0 15.5px;
  font-size: 1.2rem;
}

.Header {
  display: grid;
  justify-items: center;
  font-size: 4rem;
  color: #444444;
  padding: 20px 0 20px 0;
  font-weight: bold;
}

h2 {
  padding-bottom: 10px;
}

@media all and (max-width: 414px) {
  .ImageCard,
  #CardImage {
    width: 314px;
  }
  ul {
    font-size: 1.2rem;
    padding: 0 15px;
  }
  .fileContainer input {
    display: none;
  }
}
